/*    */

.overlay {
  /*    */
  opacity: 0;
  visibility: hidden;

  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 20;
  transition: 0.3s all;
}

/*     */

.modal {
  /*    */
  // opacity: 0;
  // visibility: hidden;

  /*      */
  // width: 100%;
  // max-width: 500px;

  /*     z-index */
  // position: fixed;
  // top: 50%;
  // left: 50%;

  // transform: translate(-50%, -50%);
  // z-index: 30; /*      */

  /*      */

  // box-shadow: 0 3px 10px -0.5px rgba(0, 0, 0, 0.2);
  // text-align: center;
  // padding: 30px;
  // border-radius: 3px;
  // background-color: #fff;
  // transition: 0.3s all;
  opacity: 0;
  visibility: hidden;
  max-width: 100%;
  max-height: 100%;
  z-index: 9999;
  text-align: center;
  overflow-y: auto;
  overflow-x: hidden;
  background-color: #fcfcfc;
  border: 2px solid #f8d24e;
  border-radius: 10px;
  padding: 26px;
  transition: all 0.3s;
  position: fixed;
  top: 50%;
  left: 50%;

  transform: translate(-50%, -50%);
  box-shadow: 0 3px 10px -0.5px rgb(0 0 0 / 20%);
}

/*        */

.modal.active,
.overlay.active {
  position: fixed;
  opacity: 1;
  visibility: visible;
}

/*     */

.modal__cross {
  width: 15px;
  height: 15px;
  position: absolute;
  top: 20px;
  right: 20px;
  fill: #444;
  cursor: pointer;
}

